import { ColumnDef, Row } from '@tanstack/react-table';
import CircularProgress from '@/components/circles/CircularProgress';
import adel from '@/assets/imgs/user.png';
import { Employee } from '@/types/employee.types';
import { useNavigate } from 'react-router-dom';
import EditIcon from '@/components/icons/EditIcon';
import ViewIcon from '@/components/icons/ViewIcon';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<Employee> }) => {
    const navigate = useNavigate();

    return (
        <div className="me-6 text-end flex flex-row items-center justify-end gap-7">
            <button
                onClick={() => navigate(`/human-resources/employee/${row.original.id}/edit`)}
                className="hover:bg-white-blue-300 rounded-full p-1"
            >
                <EditIcon className="text-primery-blue-400" />
            </button>
            <button
                onClick={() => navigate(`/human-resources/employee/${row.original.id}`)}
                className="hover:bg-white-blue-300 rounded-full p-1"
            >
                <ViewIcon className="text-primery-blue-400" />
            </button>
        </div>
    );
};

export const columns: ColumnDef<Employee>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="flex flex-row justify-start items-center gap-4 text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                <img src={adel} alt="" className="rounded-full w-10 h-10 object-cover" />
                <div className="flex flex-col gap-1">
                    <p className="tracking-normal text-primery-blue-900 font-medium text-sm">{row.original.name}</p>
                    <p className="tracking-normal text-white-blue-800 font-normal text-sm"> {row.original.email}</p>
                </div>
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    تاريخ التعيين
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-[#625E74] leading-[26.24px] font-semibold tracking-normal">
                {row.original.created_at}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    نسبة الحضور
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {/* {row.original.phone} */}
                    <CircularProgress size={48} />
                </div>
            );
        },
    },
    {
        accessorKey: 'date',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الدرجة الوظيفية
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.position}
                </div>
            );
        },
    },
    {
        accessorKey: 'service',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    القسم
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.department}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    حالة العمل
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-xs text-[#22AD5C] font-medium tracking-normal leading-[115%]">
                    <p className="border border-[#22AD5C] p-1 w-fit rounded-sm">{row.original.contract_type}</p>
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    {/* للاطلاع على الرسائل */}
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
